import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { UpdateSellingPriceComponent } from './update-selling-price.component';
const routes: Routes = [
  {
    path: '',
    component: UpdateSellingPriceComponent
  },
  {
    path: 'by-product-app',
    loadChildren: () => import('./by-product-app/by-product-app.module').then(m => m.ByProductAppModule)
  },
  {
    path: 'by-product-tiki',
    loadChildren: () => import('./by-product-tiki/by-product-tiki.module').then(m => m.ByProductTikiModule)
  },
]

@NgModule({
  declarations: [UpdateSellingPriceComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class UpdateSellingPriceModule { }
